import React from "react";
import classes from "./ChooseFile.module.css";
import { useState } from "react";
import FileUploader from "../../components/FileUploader/FileUploader";

const ChooseFile = (props) => {
  const [location, setLocation] = useState(0);

  const handleFile = (e) => {
    e.preventDefault();
    props.file(e.target.value);
  };

  const handleUploadFile = (file) => {
    props.file(file.name);
  };

  let content =
    location === 0 ? (
      <div className={classes.content}>
        <p>Enter the link: </p>
        <input type="text" placeholder="https://docsend.com/view/abcdefg" onChange={(e) => handleFile(e)} />
      </div>
    ) : (
      <div className={classes.content}>
        <p>Add file: </p>
        <FileUploader handleFile={handleUploadFile} />
      </div>
    );
  return (
    <div className={classes.chooseFile}>
      <p className={classes.title}>Choose file to convert</p>
      <ul>
        <li className={[classes.fromLink, location === 0 ? classes.active : null].join(" ")} onClick={() => setLocation(0)}>
          Docsend Link
        </li>
        <li className={[classes.fromComputer, location === 1 ? classes.active : null].join(" ")} onClick={() => setLocation(1)}>
          From Computer
        </li>
      </ul>
      {content}
    </div>
  );
};

export default ChooseFile;
