import React, { useEffect } from "react";
import classes from "./Card.module.css";
import { useState } from "react";
import ProgressBar from "../../components/ProgressBar/ProgressBar";
import ChooseFile from "../ChooseFile/ChooseFile";
import ConfirmFile from "../ConfirmFile/ConfirmFile";
import DownloadFile from "../DownloadFile/DownloadFile";

const Card = (props) => {
  const [fileName, setFileName] = useState("");
  const [progress, setProgress] = useState(0);
  const [page, setPage] = useState(1);
  const [disabled, setDisabled] = useState(true);
  const [content, setContent] = useState(null);

  const fileHandler = (file) => {
    console.log(file);
    setFileName(file);
    fileName === "" ? setDisabled(true) : setDisabled(false);
  };

  useEffect(() => {
    fileName === "" ? setDisabled(true) : setDisabled(false);
  }, [fileName]);

  const nextClickHandler = (btn) => {
    if (fileName === "") {
      return;
    }
    if (page === 3 && btn === "next") {
      // alert("confirmed");
      setPage(page + 1);
      return;
    }

    if (btn === "prev" && page === 2) {
      setFileName("");
    }

    btn === "next" ? setPage(page + 1) : setPage(page - 1);
  };

  useEffect(() => {
    switch (page) {
      case 1:
        setContent(<ChooseFile file={fileHandler} />);
        setProgress(0);
        break;
      case 2:
        setContent(<ConfirmFile fileName={fileName} />);
        setProgress(50);
        break;
      case 3:
        setContent(<DownloadFile getHome={() => setPage(1)} />);
        setProgress(100);
        break;

      default:
        break;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page]);

  let buttonText = page === 1 ? "next" : page === 2 ? "Yes, Continue" : "Confirm";

  return (
    <div className={classes.card}>
      <div className={classes.container}>
        {content}
        <div className={classes.progressBar}>
          <ProgressBar progress={progress} />
        </div>
        <div className={classes.buttons}>
          <button
            className={[classes.nextButton, disabled ? classes.disabled : null, page === 1 || page === 3 ? classes.hidden : null].join(" ")}
            onClick={() => nextClickHandler("prev")}>
            Previous
          </button>
          <button
            className={[classes.nextButton, disabled ? classes.disabled : null, page === 3 ? classes.hidden : null].join(" ")}
            onClick={() => nextClickHandler("next")}>
            {buttonText}
          </button>
        </div>
      </div>
    </div>
  );
};

export default Card;
