import React from "react";
import "react-step-progress-bar/styles.css";
import { ProgressBar, Step } from "react-step-progress-bar";

const Progress = ({ progress }) => {
  return (
    <ProgressBar percent={progress} filledBackground="linear-gradient(to right, #7fb9ec, #3b83c2)" width={500} height={15}>
      <Step transition="scale">
        {({ accomplished }) => <img style={{ filter: `grayscale(${accomplished ? 0 : 100}%)` }} width="40" src="/add.png" alt="first step" />}
      </Step>
      <Step transition="scale">
        {({ accomplished }) => <img style={{ filter: `grayscale(${accomplished ? 0 : 100}%)` }} width="40" src="/confirm.png" alt="second step" />}
      </Step>
      <Step transition="scale">
        {({ accomplished }) => <img style={{ filter: `grayscale(${accomplished ? 0 : 100}%)` }} width="40" src="/confirmed.png" alt="third step" />}
      </Step>
    </ProgressBar>
  );
};

export default Progress;
